$text-color: #26231A;
// $anchor-text-color: #CE534D;
// $background-color: #F1EED9;
$background-color: #FFFFFF;
$anchor-text-color: #0000FF;

html {
  border-top: 10px solid #000;
}

body {
	font-family: Verdana, Arial, sans-serif;
  	font-size: 0.9em;
	color: $text-color;
  	line-height: 1.5em;
	background-color: $background-color;
}

header,
main {
	padding: 0 20px;
}

/*** wrapper div for both header and main ***/
.wrapper {
}

/*** anchor tags ***/
a:link,
a:visited,
a:hover,
a:active {
	color: $anchor-text-color;
  	font-family: monospace;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

div.content {
  margin-left: -2em;
}

.main-list-item {
	font-weight: bold;
	font-size: 1.2em;
  font-family: monospace;
  padding-bottom: 1em;
}

.content ul li {
  font-size: 1.2em;
  font-family: monospace;
}

.fa-ul.main-list {
	margin-left: 0;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  width: 350px;
}

.post {
	font-size: .8em;
	font-family: Verdana, Arial;
	width: 500px;
}

.post h1, h2 {
	font-size: 1.2em;
	font-weight: bold;
}
/* list icons */
.main-list-item-icon {
	width: 36px;
	color: $text-color;
}

/*** logo ***/
.logo-container {
	text-align: center;
}

.logo {
  background: #f0f0f0;
	width: 200px;
	height: 200px;
	display: inline-block;
	background-size: cover;
  //border: 2px solid #000;
}

/*** author ***/
.author-container {
	margin-top: -13.5em;
}

.author-container h1 {
	font-size: 2.8em;
	margin-top: -5.5em;
	margin-right: -6.3em;
	display:block;
	margin-left: 5.7em;
	line-height: 1em;
}

/*** tagline ***/
.tagline-container p {
  visibility: hidden;
  width:200px;
	font-size: 1.2em;
  font-family: monospace;
}

/******/
hr {
	border: 0;
	height: 1px;
	background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), $text-color, rgba(0,0,0,0));
	background-image: -moz-linear-gradient(left, rgba(0,0,0,0), $text-color, rgba(0,0,0,0));
	background-image: -ms-linear-gradient(left, rgba(0,0,0,0), $text-color, rgba(0,0,0,0));
	background-image: -o-linear-gradient(left, rgba(0,0,0,0), $text-color, rgba(0,0,0,0));
}

/*** footer ***/
footer {
	position: fixed;
	bottom: 0;
	right: 0;
	height: 20px;
}

/*** media queries ***/
/* X-Small devices (phones, 480px and up) */
@media (min-width: 320px) {
	/* wrapper stays 480px wide past 480px wide and is kept centered */
	.wrapper {

		width: 320px;
		margin: 2% auto 0 auto;
	}
  
}

@media only screen and (max-device-width: 600ax) {

	.wrapper {

		width: 320px;
		margin: 2% auto 0 auto;
	}
	
}

/* All other devices (768px and up) */
@media (min-width: 768px) {
	.wrapper {
		width: 680px;
    		display:inline-block;
	}

	header {
		float: left;
		width: 200px;
		text-align: right;
    
	}

	.author-container h1,
	.tagline-container p {
		text-align: left;
	}

	main {
		width: 60%;
    		padding-right: 80%;
		margin-left: 10%;
		padding: 0;
	}
}
